const styles = {
  drawerPaper: {
    minHeight: '100vh',
    height: 'auto',
  },
  MenuItemDisabled: {
    color: grey500,
    textTransform: 'uppercase',
  },
  drawerHeader: {
    backgroundColor: 'rgb(38,50,56)',
    height: 57,
    paddingRight: 0,
  },
  headerIcon: {
    color: '#fff',
    cursor: 'pointer',
  },
  headerText: {
    fontSize: '1.5em',
    color: '#fff',
  },
  itemText: {
    fontSize: '1.1em',
  },
};

class MenuDrawer extends Component {
  constructor(props) {
    super(props);
    this.handleListItemClick = this.handleListItemClick.bind(this);
  }

  handleListItemClick = (home = false) => {
    window.scrollTo(0, 0);  
    this.props.fetchSearchText('');
    // home ? this.props.fetchImages() : null;
    this.props.onChange(false);
  };

  render() {
    const { classes } = this.props;

    let navigationCloseIconLeft, 
        navigationCloseIconRight;

    let _navigationCloseIcon = (
      <ListItemIcon>
        <Close className={classes.headerIcon} onClick={() => this.props.onChange(false)} />
      </ListItemIcon>
    )

    switch (true) {
      case (this.props.side==='right'):
        navigationCloseIconRight = _navigationCloseIcon;
        break;
      case (this.props.side==='left'):
        navigationCloseIconLeft = _navigationCloseIcon;
        break;
      default:
        break;
    }
    console.log(this.props.side, this.props.value);
    return (
      <Drawer
        classes={{
          paper:classes.drawerPaper
        }}
        anchor={this.props.side}
        open={this.props.value}
        onClose={() => this.props.onChange(false)}
      >
        <ListItem className={classes.drawerHeader}>
          {navigationCloseIconLeft}
          <ListItemText className={classes.headerText} primary="FindPicture" disableTypography />
          {navigationCloseIconRight}
        </ListItem>
        <Divider />
        <List>
          <ListItem button component={Link} to='' onClick={() => this.handleListItemClick(true)}>
            <ListItemIcon>
              <Home />
            </ListItemIcon>
            <ListItemText className={classes.itemText} primary="Home" disableTypography />
          </ListItem>
          <ListItem button component={Link} to='/favorites' onClick={this.handleListItemClick}>
            <ListItemIcon>
              <Grade />
            </ListItemIcon>
            <ListItemText className={classes.itemText} primary="Favorites" disableTypography />
          </ListItem>
          <ListItem button component={Link} to='/recentlywatched' onClick={this.handleListItemClick}>
            <ListItemIcon>
              <History />
            </ListItemIcon>
            <ListItemText className={classes.itemText} primary="Recently Watched" disableTypography />
          </ListItem>
        </List>
        <Divider />
        <List>
          <ListItem button component='a' href='https://gslav27.github.io/other/parallax_1.html' target='_blank' onClick={() => this.props.onChange(false)}>
            <ListItemIcon>
              <ContentLink />
            </ListItemIcon>
            <ListItemText className={classes.itemText} primary="Parallax" disableTypography />
          </ListItem>
          <ListItem button component='a' href='https://pixabay.com' target='_blank' onClick={() => this.props.onChange(false)}>
            <ListItemIcon>
              <ContentLink />
            </ListItemIcon>
            <ListItemText className={classes.itemText} primary="Pixabay.com" disableTypography />
          </ListItem>
        </List>
        <Divider />
      </Drawer>

      // <Drawer
      //   open={this.props.value}
      //   docked={false}
      //   onRequestChange={(val) => this.props.onChange(val)}
      //   openSecondary={this.props.side}
      // >
      //   <AppBar
      //     title='FindPicture'
      //     showMenuIconButton={(!this.props.side)}
      //     iconElementLeft={navigationCloseIconLeft}
      //     iconElementRight={navigationCloseIconRight}
      //     style={styles.DrawerHeader}
      //   />
      //   <div className='DrawerBody'>
      //     <MenuItem 
      //       primaryText="Home" 
      //       leftIcon={<ActionHome />} 
      //       onClick={() => this.props.onChange(false)} 
      //       containerElement={<Link to=''/>}
      //     />
      //     <MenuItem 
      //       primaryText="Favorites" 
      //       leftIcon={<ActionGrade />} 
      //       onClick={() => this.props.onChange(false)} 
      //       // containerElement={<Link to='/recentlywatched'/>}
      //     />
      //     <MenuItem
      //       primaryText="Recently Watched"
      //       leftIcon={<ActionHistory />}
      //       onClick={() => this.props.onChange(false)}
      //       containerElement={<Link to='/recentlywatched' />}
      //     />
      //     <Divider />
      //     <MenuItem 
      //       primaryText="Grid" 
      //       disabled={true} 
      //       style={styles.MenuItemDisabled} 
      //     />
      //     <MenuItem 
      //       primaryText="on" 
      //       leftIcon={<ImageGridOn />} 
      //       onClick={this.handleMenuClick} 
      //     />
      //     <MenuItem 
      //       primaryText="off" 
      //       leftIcon={<ImageGridOff />} 
      //       onClick={this.handleMenuClick} 
      //     />
      //     <Divider />
      //     <MenuItem 
      //       primaryText="Style" 
      //       disabled={true} 
      //       style={styles.MenuItemDisabled} 
      //     />
      //     <MenuItem 
      //       primaryText="default" 
      //       leftIcon={<ImageColorLens />} 
      //       onClick={this.handleMenuClick} 
      //     />
      //     <MenuItem 
      //       primaryText="grey" 
      //       leftIcon={<ImageColorLens />} 
      //       onClick={this.handleMenuClick} 
      //     />
      //     <Divider />
      //     <MenuItem 
      //       primaryText="Go to Pixabay" 
      //       leftIcon={<ContentLink />} 
      //       onClick={this.handleMenuClick} 
      //     />
      //   </div>
      // </Drawer>
    )
  }
}

MenuDrawer.propTypes = {
  fetchImages: PropTypes.func,
  fetchSearchText: PropTypes.func.isRequired,
  value: PropTypes.bool.isRequired,
  onChange: PropTypes.func.isRequired,
  side: PropTypes.string,
  classes: PropTypes.object.isRequired,
}

// const mapStateToProps = state => ({
//   // searchState: state,
//   searchText: state.search.searchText,
//   // designType: state.search.designType,
//   mobileWithTouch: state.appAdds.mobileWithTouch,
// })

const mapDispatchToProps = {
  fetchImages,
  fetchSearchText,
  // fetchWindowWidth,
  // fetchAmount,
}

export default connect(null, mapDispatchToProps)(withStyles(styles)(MenuDrawer))

