export const filtersStylesPattern = (theme) => {
  return {
    dialogPaperWidthSm: {
      // maxWidth: '100vw',
      maxHeight: '70vh',
      // width: 'inherit',
      // height: 'inherit',
      width: 460,
      // margin: 0,
    },
    formControl: {
      width: '100%',
    },
    rootSelectWrap: {
      width: '100%',
      height: '2em',
      margin: '0px',
      borderRadius: 2,
      fontSize: '1em',
      color: '#777',
      [theme.breakpoints.only('xs')]: {
        display: 'none',
      },
    },
    selectedValue: {
      height: '1em',
      // color: 'rgb(0, 0, 138)',
      color: '#d50000',
      transition: 'all 0.3s ease',
    },
    selectedValueTouch: {
      color: 'rgb(0, 0, 138)',
      textTransform: 'uppercase',
    },
    rootNativeColorSelect: {
      marginTop: 16,
      width: '100%',
      color: 'rgba(0, 0, 0, 0.87)',
      display: 'inline-flex',
      position: 'relative',
      fontSize: '1em',
      lineHeight: '1em',
      alignItems: 'center',
    },

    rootNativeControl: {
      display: 'flex',
      flexWrap: 'wrap',
      [theme.breakpoints.only('xs')]: {
        display: 'none',
      },
    },

    nativeColorInputHTML: {
      position: 'absolute',
      width: '100%',
      height: '100%',
      background: 'none',
      border: 'none',
      // opacity: 1,
      '&:focus': {
        // Show that it's not an text input
        // background:
        // theme.palette.type === 'light' ? 'rgba(0, 0, 0, 0.05)' : 'rgba(255, 255, 255, 0.05)',
        backgroundColor: 'rgba(0, 0, 0, 0.05)',
        borderRadius: 0, // Reset Chrome style
        borderColor: 'rgba(0,0,0,0)',
        color: 'transparent',
      },
    },

    rootListWrap: {
      display: 'none',
      padding: 0,
      [theme.breakpoints.only('xs')]: {
        display: 'block',
      },
      // marginBottom: '2px',

      borderBottom: '2px solid #ddd',
    },
    rootSelect: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'space-between',
    },
    select: {
      padding: '0 24px 0 12px',
      textAlign: 'center',
      lineHeight: '2em',
      // fontSize: '1.1em',
      [theme.breakpoints.only('xs')]: {
        padding: '0 24px',
      },
    },
    nativeControlColorButton: {
      textTransform: 'lowercase',
    },
    nativeColorInputRoot: {
      height: '1em',
    },
    nativeColorInput: {
      padding: '0 24px 0 12px',
      whiteSpace: 'nowrap',
      textOverflow: 'ellipsis',
      overflow: 'hidden',
    },
    nativeControlInputLabel: {
      marginLeft: 12,
      fontSize: '1em',
    },
    nativeColorInputAdornment: {
      top: 'calc(50 % - 12px)',
      right: '0',
      color: 'rgba(0, 0, 0, 0.54)',
      position: 'absolute',
      pointerEvents: 'none',
    },
    nativeColorDialogTitle: {
      padding: '7px 24px 0',
      display: 'flex',
      alignItems: 'baseline',
      '&>span': {
        fontSize: '1.2em',
        fontWeight: '500',
        width: '100%'
      },
      '&> div': {
        width: 'auto',
        padding: '0 11px 0 0',
      },
    },
    nativeColorDialogTitleListItem: {
      // flex: 1,
      // padding: '0 0 0 12px',
    },
    nativeColorDialogContent: {
      padding: '7px 20px 20px 20px',

      display: 'flex',
      justifyContent: 'center',
    },
    nativeColorDialogContentList: {
      display: 'flex',
      // flexFlow: 'column',
      flexWrap: 'wrap',
      // maxHeight: '60vh',
      '&> div':{
        width: '50%',
        padding: '0 15px',
        height: '2.4em',
      }
      // '&> div span': {
      //   marginLeft: '-5px',
      // },
    },
    nativeControlOption: {
      color: '#000',
    },
    nativeColorOption:{
      textAlign: 'center',
    },
    MenuPropsListPadding: {
      padding: '0 0 2px 0',
      boxSizing: 'border-box',
    },
    // MenuPropsItem: {
    //   justifyContent: 'space-between',
    // },
    MenuItemText: {
      padding: '0 6px 0 10px',
      color: '#000',
      width: '100%',
    },
    MenuItemIcon: {
      width: '0.6em',
      height: '0.6em',
      fill: 'rgba(0,0,0,0.8)',
    },
    MenuItemIconMultipleSelection: {
      width: '0.6em',
      height: '0.6em',
      fill: 'rgba(0,0,0,0.8)',
      fontSize: '1.8em',
    },
    MenuItemDisabled: {
      opacity: '1',
      paddingTop: '0.8em',
      color: '#fff',
      backgroundColor: '#666',
    },
    MenuItemDisabledText: {
      textTransform: 'uppercase',
    },
    MenuItemDisabledHook: {
      visibility: 'hidden',
    },
    backDropRoot: {
      backgroundColor: 'rgba(0,0,0,0.2)',
    },
    listItemHeader: {
      height: '2em',
      transition: 'all 0.3s ease',
      backgroundColor: '#fff',
    },
    
    listItemHeaderHidden: {
      '& > div > span > span > span' : {
        // color: '#999',
        opacity: '0.5',
      },
      // '& > div > span': {
      //   // color: '#999',
      //   opacity: '0.5',
      // },
      // backgroundColor: '#ddd',
    },
    listItemHeaderOpen: {
      // borderBottom: '2px solid #d50000',
      backgroundColor: 'rgba(0, 0, 0, 0.1)',
      // '& > div > span': {
      //   // color: '#999',
      //   opacity: '1',
      // },
      '&:hover': {
        backgroundColor: 'rgba(0, 0, 0, 0.15)',
      },
    },
    listItemHeaderButton: {
      '&:hover': {
        // backgroundColor: 'rgba(38,50,56,0.12)',
        // backgroundColor: 'rgba(207, 216, 220, 1)',
      },
    },
    listItemTextHeader: {
      fontSize: '1.05em', 
      whiteSpace: 'nowrap',
      textOverflow: 'ellipsis',
      overflow: 'hidden',
    },
    listItemTextNested: {
      fontSize: '1.05em',
      textTransform: 'uppercase',
      color: '#0d47a1',
    },
    listItemNested: {
      height: '2em',
    },
    listItemNestedButton: {
      // '&:hover': {
      //   backgroundColor: 'rgba(38,50,56,0.04)',
      // },
    },
    listCollapsedItems: {
      overflowY: 'scroll',
      maxHeight: '45vh',
      borderTop: '1px solid #d50000',
      borderBottom: '1px solid #d50000',
    },
    selectedMenuPaper: {
      maxWidth: 300,
    },

    // for 'native' color menu
    selectedMenuModal: {
      justifyContent: 'center',
      alignItems: 'center',
    },
    selectedMenuPaperRoot: {
      // top: '50%',
      // left: '50%',
      // transform: 'translate(-50%, -50%)',
      position: 'static',
      maxHeight: '80vh',
    },
    overflow: {
      visibility: 'hidden',
      position: 'absolute',
      width: '100%',
      height: '1em',
      transition: 'all 0.25s ease',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      backgroundColor: 'rgba(255, 255, 255, 0)',
      color: '#fff',
    },
    overflowBottom: {
      // bottom: 0,
      // marginBottom: -1,
      bottom: 1,
    },
    overflowTop: {
      top: '2em',
      // marginTop: -1,
      marginTop: 1,
    },
    overflowBottomNative: {
      bottom: 0,
      // paddingRight: 12,
      marginLeft: '-6px',
    },
    overflowTopNative: {
      top: 55,
      borderTop: '1px solid rgba(0, 0, 0, 0.12)',
      // paddingRight: 12,
      marginLeft: '-6px',
    },
  }
}


