import { 
  VIEWER_LOAD_IMG,
  VIEWER_OPEN,
  VIEWER_WAIT_API_RESPONSE,
} from './types';

import { updateRecentlyWatched } from './appAddsAction';

export const fetchViewerOpen = (open, currentImgInd) => (dispatch) => {
  dispatch({
    type: VIEWER_OPEN,
    payload: [open, currentImgInd],
  });
  if (open) {
    dispatch(updateRecentlyWatched(currentImgInd))
  }
}

export const fetchViewerImg = (newImgInd = null) => (dispatch, getState) => {
  if(newImgInd === null) {
    newImgInd = getState().imgViewer.currentImgInd + 1;
  }
  dispatch({
    type: VIEWER_LOAD_IMG,
    payload: newImgInd,
  });
  dispatch(updateRecentlyWatched(newImgInd))
}

export const fetchWaitNextData = (loading = true) => (dispatch) => {
  dispatch({
    type: VIEWER_WAIT_API_RESPONSE,
    payload: loading,
  })
}


