import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import throttle from 'lodash.throttle';

import { 
  fetchImages,
  fetchAmount,
  fetchOrder,
  fetchCategory,
  fetchColor,
  fetchImageType,
  fetchOrientation,
  fetchPage,
} from '../actions/searchAction';
import { fetchSearchText } from '../actions/searchAction';
// import { fetchWindowWidth } from '../actions/searchAction';

import MenuDrawer from './Drawer';
import SearchFilters from './Filters';

import { withStyles } from '@material-ui/core/styles';
import compose from 'recompose/compose';
import withWidth from '@material-ui/core/withWidth';

import IconButton from '@material-ui/core/IconButton';
import Input from '@material-ui/core/Input';
import AppBar from '@material-ui/core/AppBar';

import Search from '@material-ui/icons/Search';
import Menu from '@material-ui/icons/Menu';
import MoreVert from '@material-ui/icons/MoreVert';
import Close from '@material-ui/icons/Close';
import FilterList from '@material-ui/icons/FilterList';

const styles = theme => ({
  appBar: {
    backgroundColor: 'rgb(38,50,56)',
    height: 57,
    padding: '0px 4px',
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    borderBottom: '1px solid #ddd',
    transition: 'border-bottom 0.2s ease',
  },
  appBarBorder: {
    borderBottom: '1px solid rgb(38, 50, 56)',
    [theme.breakpoints.only('xs')]: {
      borderBottom: '1px solid #ddd',
    },
  },
  appBarInputGroup: {
    marginLeft: 4,
    width: '65%',
    display: 'flex',
    alignItems: 'center',
    [theme.breakpoints.only('xs')]: {
      width: '100%',
    },
    [theme.breakpoints.only('sm')]: {
      width: '75%',
    },
    [theme.breakpoints.only('md')]: {
      width: '70%',
    },
  },
  appBarLastChildGroup: {
    display: 'inline-flex',
  },
  appBarIcons: {
    color: '#fff',
  },
  filtersButton: {
    height: '1.8em',
    width: '1.8em',
    transition: 'visibility 0.2s ease',
    [theme.breakpoints.only('xs')]: {
      width: '2em',
      height: '2em',
    },
    '& svg': {
      transition: 'all 0.2s ease',
    },
  },
  filtersButtonTop: {
    visibility: 'hidden',
    '& svg': {
      color: 'rgb(38, 50, 56)',
    },
    [theme.breakpoints.only('xs')]: {
      visibility: 'visible',
      '& svg': {
        color: '#fff',
      },
    },
  },
  deleteSearchTextButton: {
    height: '1.6em',
    width: '1.6em',
    padding: '0',
    transition: 'visibility 1s ease',
    visibility: 'hidden',
  },
  deleteSearchTextButtonDisplay: {
    visibility: 'visible',
  },
  inputFieldIcons: {
    color: 'rgb(38, 50, 56)',
  },
  hidden: {
    visibility: 'hidden',
  },
  searchTextIconWrap: {
    display: 'flex',
    justifyContent: 'center',
    height: '100%',
    width: '4em',
    backgroundColor: '#ffeb3b',
    borderRadius: '0px 2px 2px 0px',
    borderLeft: '1px solid #aaa',
    [theme.breakpoints.only('xs')]: {
      borderRadius: '0px 10px 0px 0px',
    },
  },
  searchTextButton: {
    height: '1.6em',
    width: '1.6em',
    padding: '0',
  },
  rootComponent: {
    position: 'fixed',
    width: '100%',
    top: 0,
    zIndex: 1001,
    transition: 'all 0.1s',
  },
  rootComponentHide: {
    transform: 'translate(0px, -57px)',
    [theme.breakpoints.only('xs')]: {
      transform: 'none',
    }
  },
  searchFieldFormControl: {
    width: '100%',
  },
  searchFieldInput: {
    padding: '6px 0px 4px',
    width: '100%',
    // fix .MuiInput-inputType: {height: 1.1875em}
    height: 'initial',
  },
  searchFieldInputRoot: {
    width: '100%',
    paddingLeft: 5,
    fontSize: '1.2em',
    lineHeight: '1.2em',
    height: '2em',
  },
  searchFiltersTop: {
    position: 'relative',
    top: 57,
    [theme.breakpoints.only('xs')]: {
      display: 'none',
    },
  },
  searchFiltersBody: {
    position: 'fixed',
    top: 0,
    height: 57,
    width: '100%',
    display: 'flex',
    alignItems: 'end',
    transition: 'all 0.3s',
    [theme.breakpoints.only('xs')]: {
      top: -200,
    },
  },
  searchFiltersBodyTransform: {
    transform: 'translate(0px, 57px)',
    [theme.breakpoints.only('xs')]: {
      transform: 'translate(0px, 257px)',
    },
  },
  searchFieldWrap: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    width: '100%',
    height: '2em',
    backgroundColor: '#f5f5f5',
    borderRadius: 1,
  },
  searchFieldWrapMobile: {
    borderRadius: '2px',
    [theme.breakpoints.only('xs')]: {
      borderRadius: '0px 10px 0px 0px',
    },
  },
})

class NavBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showDrawer: false,
      showFilters: false,
      windowTop: true,
      windowPageYOffset: 0,
      hideHeader: false,
      inputOnFocus: false,
    };
    this.handleScroll = this.handleScroll.bind(this);
    this.handleScrollThrottled = throttle(this.handleScroll, 250);
    this.handleDeleteSearchText = this.handleDeleteSearchText.bind(this);
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScrollThrottled);
    // this.props.fetchAmount(this.props.width);
    // this.props.fetchImages();
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScrollThrottled.cancel());
  }

  componentDidUpdate = (prevProps, prevState) => {
    if (this.props.width !== prevProps.width) {
      (this.props.width === 'xs') ? this.setState({ showFilters: false }) : null;
      (this.props.width !== 'xs') ? this.setState({ hideHeader: false }) : null;
    };
  }

  handleScroll() {
    let currentYOffset = window.pageYOffset;
    let prevYOffset = this.state.windowPageYOffset;
    let hideHeader = this.state.hideHeader;
    if (currentYOffset < 40) {
      this.setState({
        windowTop: true,
        hideHeader: false,
      })
    } else {
      this.setState({
        windowTop: false,
      });
    }
    // hiding NavBar on Scroll Down (and showing it on Scroll Up) in mobile devices with touch screen
    if (this.props.mobileWithTouch) {
      this.setState({
        windowPageYOffset: currentYOffset,
      });
      switch (true) {
        case (currentYOffset < 150):
          this.setState({
            hideHeader: false,
          });
          break;
        case (((currentYOffset - prevYOffset) > 20) && !hideHeader):
          this.setState({
            hideHeader: true,
            showFilters: false,
          });
          break;
        case (((currentYOffset - prevYOffset) < -10) && hideHeader):
          this.setState({
            hideHeader: false,
          });
          break;
        default:
          break;
      }
    } 
  }

  handleMenuButtonClick = () => {
    this.setState({
      showDrawer: !this.state.showDrawer,
    })
  }

  handleFiltersButtonClick = () => {
    this.setState({
      showFilters: !this.state.showFilters,
    })
  }

  handleDeleteSearchText = () => {
    this.props.fetchSearchText('');
    this.props.fetchImages()
    this.searchTextInput.focus();
  }

  render() {
    const { mobileWithTouch, classes, location, width } = this.props;

    let searchHint = 'Search...';

    let filtersType,
        navigationMenuIcon,
        navigationMoreVertIcon,
        searchIcon;

    switch (true) {
      // case ((location.pathname === '/') || (location.pathname === `/q_${this.props.searchText}`)):
      //   filtersType = <SearchFilters showFiltersBar={this.state.showFilters} onChange={() => this.setState({ showFilters: false })} />
      //   break;
      case (location.pathname === '/favorites'):
        filtersType = null
        break;
      case (location.pathname === '/recentlywatched'):
        filtersType = null
        break;
      default:
        filtersType = <SearchFilters showFiltersBar={this.state.showFilters} onChange={() => this.setState({ showFilters: false })} />
        break;
    }

    const _deleteSearchTextIcon = (
      <IconButton
        onClick={this.handleDeleteSearchText}
        className={`${classes.deleteSearchTextButton} ${(this.props.searchText.length > 0) ? classes.deleteSearchTextButtonDisplay : null}`}
      >
        <Close className={classes.inputFieldIcons}/>
      </IconButton>
    );

    const _searchTextIcon = (
      <div className={classes.searchTextIconWrap}>
        <IconButton
          onClick={() => this.state.inputOnFocus ? (this.props.fetchImages(), this.setState({inputOnFocus: false })) : this.searchTextInput.focus()}
          className={classes.searchTextButton}
        >
          <Search className={classes.inputFieldIcons} />
        </IconButton>
      </div>
    )

    const _filtersIcon = (
      <IconButton
        onClick={this.handleFiltersButtonClick}
        className={`${classes.filtersButton} ${this.state.windowTop ? classes.filtersButtonTop : null }`} 
      >
        <FilterList className={classes.appBarIcons} />
      </IconButton>
    );

    const _navigationMenuIcon = (
      <IconButton
        onClick={this.handleMenuButtonClick}
      >
        <Menu className={classes.appBarIcons} />
      </IconButton>
    );

    const _navigationMoreVertIcon = (
      <IconButton
      // onClick={this.handleMenuButtonClick}
      >
        <MoreVert className={classes.appBarIcons} />
      </IconButton>
    );

    const _searchIcon = (
      <IconButton
        className={mobileWithTouch ? classes.hidden : null}
        onClick={() => this.searchTextInput.focus()}
      >
        <Search className={classes.appBarIcons} />
      </IconButton>
    );

    const _searchFiltersTop = (
      <div className={`${classes.searchFiltersTop} ${this.state.showFilters ? classes.hidden : null}`}>
        {filtersType}
      </div>
    );

    const _searchFiltersBody = (
      <div className={`${classes.searchFiltersBody} ${this.state.showFilters ? classes.searchFiltersBodyTransform : null}`} >
        {filtersType}
      </div>
    );

    // if (this.props.designType === 'xs') {
    if (width === 'xs') {
      searchIcon = null;
      navigationMenuIcon = true;
      navigationMoreVertIcon = false;
    } else {
      searchIcon = _searchIcon;
      navigationMenuIcon = false;
      navigationMoreVertIcon = true;
    }

    const menuDrawer = (
      <MenuDrawer
        value={this.state.showDrawer}
        side={navigationMenuIcon ? 'right' : 'left'}
        onChange={(val) => this.setState({ showDrawer: val })}
      />
    );

    // console.log(this.props.location);
    // console.log('NavBar state: ', this.state);
    // console.log('inputOnFocus', this.state.inputOnFocus);
    // console.log('this.props', this.props);

    return (
      <div>
        <div className={`${classes.rootComponent} ${this.state.hideHeader ? classes.rootComponentHide : null}`}>
          <AppBar className={`${classes.appBar} ${(this.state.windowTop || this.state.showFilters) ? classes.appBarBorder : null}`}>
            {(!navigationMenuIcon) ? _navigationMenuIcon : null}
            <div className={classes.appBarInputGroup}>
              {/* {touch ? null : searchIcon} */}
              {searchIcon}
              <div className={`${classes.searchFieldWrap} ${mobileWithTouch ? classes.searchFieldWrapMobile : null}`}>
                <form 
                  className={classes.searchFieldFormControl} 
                  action='#' 
                  onSubmit={
                    (e) => {
                      e.preventDefault();
                      this.searchTextInput.blur();
                      mobileWithTouch ? this.props.fetchImages() : null
                    }
                  } 
                > 
                  <Input
                    type={mobileWithTouch ? 'search' : null}
                    classes={{
                      root: classes.searchFieldInputRoot,
                      input: classes.searchFieldInput,
                    }}
                    value={this.props.searchText}
                    onChange={
                      (e) => {
                        this.props.fetchSearchText(e.target.value); 
                        // mobileWithTouch ? null : this.props.fetchImages(e.target.value)
                        mobileWithTouch ? null : this.props.fetchImages()
                      }
                    }
                    placeholder={searchHint}
                    disableUnderline={true}
                    autoFocus={mobileWithTouch ? false : true}
                    inputRef={input => this.searchTextInput = input}
                    onFocus={() => this.setState({ inputOnFocus : true})}
                  />
                </form>
                {_deleteSearchTextIcon}
                {mobileWithTouch ? _searchTextIcon : null}
              </div>
              {_filtersIcon}
            </div>
            <div className={classes.appBarLastChildGroup}>
              {navigationMoreVertIcon ? _navigationMoreVertIcon : null}
              {navigationMenuIcon ? _navigationMenuIcon : null}
            </div>
          </AppBar>
          {_searchFiltersBody}
          {this.state.showDrawer ? menuDrawer : null}
        </div>
        {_searchFiltersTop}
      </div>
    )
  }
}

NavBar.propTypes = {
  fetchSearchText: PropTypes.func.isRequired,
  fetchImages: PropTypes.func.isRequired,
  fetchAmount: PropTypes.func.isRequired,
  fetchOrder: PropTypes.func,
  fetchCategory: PropTypes.func,
  fetchColor: PropTypes.func,
  fetchImageType: PropTypes.func,
  fetchOrientation: PropTypes.func,
  fetchPage: PropTypes.func,
  // fetchWindowWidth: PropTypes.func.isRequired,
  searchText: PropTypes.string.isRequired,
  // designType: PropTypes.string.isRequired,
  mobileWithTouch: PropTypes.bool.isRequired,
  classes: PropTypes.object.isRequired,
  width: PropTypes.string.isRequired,
  order: PropTypes.string,
  orientation: PropTypes.string,
  imageType: PropTypes.string,
  category: PropTypes.string,
  color: PropTypes.array,
  page: PropTypes.number,
  amount: PropTypes.number,
}

const mapStateToProps = state => ({
  // searchState: state,
  searchText: state.search.searchText,
  // designType: state.search.designType,
  mobileWithTouch: state.appAdds.mobileWithTouch,
  order: state.search.order,
  orientation: state.search.orientation,
  imageType: state.search.imageType,
  category: state.search.category,
  color: state.search.color,
  page: state.search.page,
  amount: state.search.amount,
})

const mapDispatchToProps = {
  fetchImages,
  fetchSearchText,
  // fetchWindowWidth,
  fetchAmount,
  fetchOrder,
  fetchCategory,
  fetchColor,
  fetchImageType,
  fetchOrientation,
  fetchPage,
}

export default connect(mapStateToProps, mapDispatchToProps)(compose(withStyles(styles), withWidth(), )(NavBar))


