import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';
import compose from 'recompose/compose';
import withWidth from '@material-ui/core/withWidth';

import ImgOrientation from './SearchFilters/ImgOrientation';
import ImgCategory from './SearchFilters/ImgCategory';
import ImgColor from './SearchFilters/ImgColor';
import ImgType from './SearchFilters/ImgType';
import ImgOrder from './SearchFilters/ImgOrder';

import Divider from '@material-ui/core/Divider';

const styles = theme => ({
  filtersBarWrap: {
    width: '100%',
  },
  filtersBar:{
    display: 'flex',
    flexFlow: 'row',
    justifyContent: 'space-evenly',
    backgroundColor: '#fff',
    paddingTop: '3px',
    margin: '0px 2px',
    borderBottom: '2px solid #fff',
    fontSize: '1em',
    [theme.breakpoints.down('md')]: {
      justifyContent: 'space-around',
    },
    [theme.breakpoints.only('xs')]: {
      flexFlow: 'column',
    },
    '& > div': {
      maxWidth: '20%',
      [theme.breakpoints.only('xs')]: {
        maxWidth: '100%',
      },
      '&:hover': {
        // backgroundColor: '#eee',
        borderRadius: 2,
      },
    }
  },
  filtersBarBackdrop: {
    // visibility: 'hidden',
    display: 'none',
    // position: 'relative',
    height: '100vh',
    width: '100vw',
    bottom: 0,
    zIndex: -1,
    backgroundColor: 'rgba(0, 0, 0, 0.25)',
    transition: 'all 0.3s ease',
    [theme.breakpoints.only('xs')]: {
      // visibility: 'visible',
      display: 'block',
    },
  },
})

class SearchFilters extends Component {
  constructor(props) {
    super(props);
    this.state = {
      collapse: {
        order: false,
        orientation: false,
        category: false,
        color: false,
        type: false,
      },
      someItemCollapsed: false,
    }
  }

  static getDerivedStateFromProps = (props, state) => {
    if (props.showFiltersBar !== true) {
      state.someItemCollapsed = false;
    }
    return null
  }

  handleItemCollapse = (filter, val) => {
    // create new copy of this.state.collapse with all 'filters' set to 'false'
    let newState = {};
    for (const key in this.state.collapse) {
      newState[key] = false;
    };
    // set current collapse value of handled filter
    newState[filter] = val,
    // update this.state.collapse
    this.setState({
      collapse: newState,
      someItemCollapsed: val ? true : false,
    });
  }

  render() {
    const { classes, showFiltersBar, mobileWithTouch } = this.props;
    const { collapse, someItemCollapsed } = this.state;
    // console.log(this.state.someItemCollapsed);

    return (
      <div className={classes.filtersBarWrap}>
        <div className={`${classes.filtersBar} filtersBar`}>
          <div> <ImgOrder showFiltersBar={showFiltersBar} collapse={collapse.order} someItemCollapsed={someItemCollapsed} onChange={(val) => this.handleItemCollapse('order', val)} /> </div>
          <div> <ImgOrientation showFiltersBar={showFiltersBar} collapse={collapse.orientation} someItemCollapsed={someItemCollapsed} onChange={(val) => this.handleItemCollapse('orientation', val)}/> </div>
          <div> <ImgCategory showFiltersBar={showFiltersBar} collapse={collapse.category} someItemCollapsed={someItemCollapsed} onChange={(val) => this.handleItemCollapse('category', val)}/> </div>
          <div> <ImgColor showFiltersBar={showFiltersBar} collapse={collapse.color} someItemCollapsed={someItemCollapsed} onChange={(val) => this.handleItemCollapse('color', val)}/> </div>
          <div> <ImgType showFiltersBar={showFiltersBar} collapse={collapse.type} someItemCollapsed={someItemCollapsed} onChange={(val) => this.handleItemCollapse('type', val)}/> </div>
        </div>
        <Divider />
        {/* add backdrop for mobile devices with touch screen && 'xs'-width */}
        {(mobileWithTouch && showFiltersBar) ? <div className={classes.filtersBarBackdrop} onClick={this.props.onChange}></div> : null}
      </div>
    )
  }
}

SearchFilters.propTypes = {
  showFiltersBar: PropTypes.bool,
  // designType: PropTypes.number.isRequired,
  classes: PropTypes.object.isRequired,
  // width: PropTypes.string.isRequired,
  mobileWithTouch: PropTypes.bool.isRequired,
}

const mapStateToProps = state => ({
  // designType: state.search.designType,
  mobileWithTouch: state.appAdds.mobileWithTouch,
})

export default connect(mapStateToProps, {})(compose(withStyles(styles), withWidth(), )(SearchFilters))


