import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { Link } from 'react-router-dom';

import { fetchImages, fetchPage, fetchSearchText } from '../actions/searchAction';
import { addToFavorites } from '../actions/appAddsAction';

import { withStyles } from '@material-ui/core/styles';
import compose from 'recompose/compose';
import withWidth from '@material-ui/core/withWidth';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';

// import OpenInNew from '@material-ui/icons/OpenInNew';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import StarIcon from '@material-ui/icons/Star';
import Chat from '@material-ui/icons/Chat';
import PageView from '@material-ui/icons/Pageview';
import GetApp from '@material-ui/icons/GetApp';

const styles = theme => ({
  barTitleTag: {
    display: 'inline-block',
    paddingRight: '0.35em',
    textDecoration: 'none',
    color: '#333',
    '&:hover': {
      backgroundColor: '#ddd',
      borderRadius: 2,
    },
  },
  card: {
    padding: '5px 0px',
  },
  cardActions: {
    padding: '0px 4px 4px',
  },
  cardBr: {
    display: 'block',
    [theme.breakpoints.only('xs')]: {
      display: 'none',
    },
  },
  cardHeader: {
    paddingTop: '0.5em',
    paddingBottom: '0.5em',
  },
  cardMedia: {
    width: '100%',
    maxHeight: '90vh',
    objectFit: 'scale-down',
  },
  cardContent: {
    padding: '0.8em 16px 0 16px',
  },
  // gridList: {
  //   margin: '0',
  //   justifyContent: 'space-around',
  // },
  // gridCell: {
  //   cursor: 'pointer',
  // },
  noMatchesWrapper: {
    display: 'flex',
    posititon: 'fixed',
    top: '0',
    left: '0',
    width: '100%',
    height: '80vh',
    alignItems: 'center',
    justifyContent: 'center',
  },
  noMatches: {
    width: '90vw',
    textAlign: 'center',
    fontSize: '2.5em',
    fontWeight: 'bold',
    color: '#aaa',
  },
  rootComponent: {
    position: 'relative',
    top: 67,
    display: 'block',
    margin: 'auto',
    color: '#550',
    // backgroundColor: '#bbf',
    width: '70%',
    // float: 'right',
    [theme.breakpoints.only('xs')]: {
      top: 57,
      width: '100%',
    },
  },
  socialData: {
    color: '#333',
    fontSize: '0.95em',
    marginLeft: '-7px',
  },
  viewerIconButton: {
    maxHeight: '10vh',
    // padding: 1,
    // '&:hover svg': {
    //   maxWidth: '10vh',
    //   maxHeight: '10vh',
    //   height: '1.8em',
    //   width: '1.8em',
    //   transition: 'all 0.2s ease',
    // },
  },
  viewerIconStyle: {
    color: '#333',
    height: '1.3em',
    width: '1.3em',
  },
});

class RecentlyWathced extends Component {
  componentDidMount = () => {
    window.scrollTo(0, 0);
  }

  handleAddToFavorites = (index) => {
    this.props.addToFavorites(index, 1);
    this.forceUpdate();
  }

  handleOnTagClick = (tag) => {
    this.props.fetchSearchText(tag);
    this.props.fetchPage(1);
    this.props.fetchImages();
  }

  formatData = (data) => {
    let formattedData;
    switch (true) {
      case (data < 1000):
        formattedData = data;
        break;
      case (data < 1000000):
        formattedData = (data / 1000).toFixed(0) + 'K';
        break;
      default:
        formattedData = (data / 1000000).toFixed(0) + 'M';
        break;
    }
    return formattedData
  }

  render() {
    console.log('Start render RecentlyWatched');

    let imageListContent;
    const { favorites, recentlyWatchedImgs, /*images, designType,*/ classes } = this.props;

    let isFavorite = (imgId) => favorites.findIndex(favImg => favImg.id === imgId);

    if (recentlyWatchedImgs.length) {
      imageListContent = (
        recentlyWatchedImgs.map((img, index) => {
          return (
            <div key={img.id}>
              <Card className={classes.card}>
                <CardHeader
                  className={classes.cardHeader}
                  avatar={
                    <Avatar>
                      <img src={img.userImageURL} alt={null} />
                    </Avatar>
                  }
                  action={null
                    // <IconButton>
                    //   <MoreVertIcon />
                    // </IconButton>
                  }
                  title={
                    <a className={classes.barTitleTag}
                      href={`https://pixabay.com/users/${img.user}-${img.user_id}/`}
                      target='_blank'
                      title={`'${img.user}' profile at Pixabay`}
                    >
                      {img.user}
                    </a>
                  }
                // subheader={img.tags}
                />
                <CardMedia
                  className={classes.cardMedia}
                  component='img'
                  src={img.largeImageURL}
                  title={img.tags}
                />
                <CardContent className={classes.cardContent}>
                  {(() => {
                    let tagArray;
                    if (img.tags.split(',').length > 1) {
                      tagArray = img.tags.split(',')
                    } else {
                      tagArray = img.tags.split(' ')
                    }
                    return (
                      tagArray.map((tag, tagIndex) => (
                        <Link to='/'
                          className={classes.barTitleTag}
                          key={tagIndex}
                          title={`search for '${tag.trim()}'`}
                          onClick={() => this.handleOnTagClick(tag.trim())}
                        >
                          #{tag.trim()}
                        </Link>
                      )))
                  })()
                  }
                </CardContent>
                <CardActions className={classes.cardActions} disableActionSpacing>
                  <IconButton
                    className={classes.viewerIconButton}
                    onClick={() => this.handleAddToFavorites(index)}
                    title={!~isFavorite(img.id) ? 'add to Favorites' : 'remove from Favorites'}
                  >
                    {!~isFavorite(img.id) ? <StarBorderIcon className={classes.viewerIconStyle} /> : <StarIcon className={classes.viewerIconStyle} />}
                  </IconButton>
                  <div className={classes.socialData}>
                    {this.formatData((!~isFavorite(img.id) ? img.favorites : (img.favorites + 1)))} 
                  </div>
                  <IconButton
                    className={classes.viewerIconButton}
                    title='read at Pixabay'
                    href={img.pageURL}
                    target='_blank'
                  >
                    <Chat className={classes.viewerIconStyle} />
                  </IconButton>
                  <div className={classes.socialData}> {this.formatData(img.comments)} </div>
                  <IconButton
                    className={classes.viewerIconButton}
                    title='see at Pixabay'
                    href={img.pageURL}
                    target='_blank'
                  >
                    <PageView className={classes.viewerIconStyle} />
                  </IconButton>
                  <div className={classes.socialData}> {this.formatData(img.views)} </div>
                  <IconButton
                    className={classes.viewerIconButton}
                    title='download from Pixabay'
                    href={img.pageURL}
                    target='_blank'
                  >
                    <GetApp className={classes.viewerIconStyle} />
                  </IconButton>
                  <div className={classes.socialData}> {this.formatData(img.downloads)} </div>
                  {/* <IconButton
                    className={classes.viewerIconButton}
                    title='see at Pixabay'
                    href={img.pageURL}
                    target='_blank'
                  >
                    <OpenInNew className={classes.viewerIconStyle} />
                  </IconButton> */}
                </CardActions>
              </Card>
              <br className={classes.cardBr} />
            </div>
          )
        })
      )
    } else {
      imageListContent = (
        <div className={classes.noMatchesWrapper}>
          <div className={classes.noMatches}>
            unfortunately you haven't watched any images yet
          </div>
        </div>
      )
    }
    return (
      <div className={classes.rootComponent}>
        {imageListContent}
      </div>
    )
  }
}

RecentlyWathced.propTypes = {
  fetchImages: PropTypes.func,
  fetchPage: PropTypes.func,
  fetchSearchText: PropTypes.func,
  // fetchMoreImages: PropTypes.func,
  // fetchViewerOpen: PropTypes.func,
  recentlyWatchedImgs: PropTypes.array,
  // images: PropTypes.array,
  // totalHits: PropTypes.number,
  // page: PropTypes.number,
  // designType: PropTypes.number,
  // orientation: PropTypes.string,
  // open: PropTypes.bool,
  // searchText: PropTypes.string,
  favorites: PropTypes.array,
  classes: PropTypes.object.isRequired,
  width: PropTypes.string.isRequired,
}

const mapStateToProps = state => ({
  recentlyWatchedImgs: state.appAdds.recentlyWatchedImgs,
  // images: state.search.images,
  // totalHits: state.search.total,
  // page: state.search.page,
  // designType: state.search.designType,
  // orientation: state.search.orientation,
  // open: state.imgViewer.open,
  favorites: state.appAdds.favorites,
  addToFavorites: PropTypes.func,
})

const mapDispatchToProps = {
  addToFavorites,
  fetchImages,
  fetchPage,
  fetchSearchText,
}

export default connect(mapStateToProps, mapDispatchToProps)(compose(withStyles(styles), withWidth(), )(RecentlyWathced))


